:root{
  --navbar-height: 50px;

  --Color1: #095256; /*Deep Jungle Green*/
  --Color2: #087F8C; /*Metallic Seaweed*/
  --Color3: #5AAA95; /*Polished Pine*/
  --Color4: #86A873; /*Asparagus*/
  --Color5: #BB9F06; /*Lemon Curry*/
  --Color6: #D18804;

}

a{text-decoration: unset;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

body{
  background: linear-gradient(45deg, var(--Color2), var(--Color3));
}


.App{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 30px;

  min-height: 100vh;
  padding: 16px;
}

.calculator{
  background-color: white;

  width: 100%;
  max-width: 400px;

  border-radius: 16px;
  overflow: hidden;

  box-shadow: 0px 2px 64px rgba(0,0,0,0.2);
  border-style: solid;
  border-color: var(--Color5);
}

#display{
  background-color: var(--Color1);

  padding: 16px;

  text-align: right;
  color: white;
  font-size: 24px;
  font-weight: 300;
}

#display span {
  font-size: 14px;
  color: grey;
}

button{
  appearance: none;
  border: none;
  outline: none;
  color: white;
  font-size: 20px;
  padding: 16px;

  cursor: pointer;
  transition: 0.4s;
}

button:hover{
  opacity: 0.9;
}

.operators{
  display: flex;
}

.operators button{
  background-color: var(--Color5);
  flex: 1 1 0%;
}

.digits{
  display: flex;
  flex-wrap: wrap;  
}

.digits button{
  background-color: var(--Color2);
  
  flex: 1 1 33.333%;
  max-width: 33.333%;
}

#navigation-container{
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;    

  column-gap: 30px;
  
}
#return-btn, #github-btn{
  display: flex;
  justify-content: center;
  align-items: center; 

  background-color: var(--Color1);
  box-shadow: 0px 0px 15px var(--Color2);

  border-radius: 10px;

  font-size: 1.6em;
  font-weight: 100;
  color: white;
  text-shadow: 0px 0px 2px grey;

  padding: 20px;

}

@media only screen and (max-width: 400px){
  #navigation-container{
    column-gap: 10px;
  }
  #return-btn, #github-btn{
    font-size: 1.4em;
  }

}